const regexEmail = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
const regexPassword = /(?=(.*[0-9]))(?=.*[\!@#$%^&*()\\[\]{}\-_+=|:;"'<>,./?])(?=.*[a-z])(?=(.*[A-Z]))(?=(.*))/

export function validate(inputs) {
	const errors = {}
	if (!regexEmail.test(inputs.user)) errors.user = "Debe ser un correo electrnico"
	if (!inputs.user.length) errors.user = "No debe estar vacio"
	if (inputs.user.length>36) errors.user = "No puede tener ms de 35 caracteres."
	if (!regexPassword.test(inputs.password)) errors.password = "La contrasea debe tener una letra minscula, una letra mayscula, un nmero, un carcter especial"
	if (inputs.password.length<6) errors.password = "La contrasea debe terner minimo 6 caracteres"
	if (inputs.password.length>10) errors.password = "La contrasea debe terner maximo 10 caracteres"
	return errors
}